figure {
  margin: 0;
}

.podkit-logo {
  position: relative;
  border: 1px solid white;
}

.podkit-logo__button {
  position: absolute;
  bottom: 4px;
  right: 4px;
  background-color: hsla(0, 0%, 100%, 1);
  border: 1px solid grey;
}

.podkit-title {
  margin: 3px 3px 0 0;
}

.podkit-cta a {
  color: var(--podkit-font-highlight-color) !important;
}

.podkit-url::before {
  margin-right: 0.5em;
  content: "â†³";
}

.podkit-url {
  display: flex;
  margin-left: calc(20% + 2em);
  padding: 0.5em 0.5em 0.5em 0;
  font-family: sans-serif;
}

.podkit-url__input {
  margin: 0 0 0 0.5em;
  padding: 0 0.5em;
  border: 1px solid hsl(0, 0%, 50%);
  border-radius: 3px;
}

.podkit-cta .editor-url-input__button {
  align-self: center;
}

.podkit-cta form.editor-url-input__button-modal {
  position: absolute;
}

.podkit-dropdown {
  display: inline-block;
  margin-left: 0.5em;
  align-self: center;
}

.podkit-dropdown .components-button:active:enabled {
  vertical-align: inherit;
}

.podkit-dropdown__input {
  padding: 8px 8px 0;
}

.podkit-expanded .editor-rich-text__inline-toolbar {
  justify-content: start;
}

.editor-styles-wrapper h3.podkit-title {
  font-weight: lighter;
}

.wp-block[data-align="full"] .podkit-block {
  padding: 3em;
  box-shadow: none;
}
